import React, { useEffect, useState } from 'react';

const GetBearerToken = () =>{
    const [bearerToken, setBearerToken] = useState({});
    useEffect(() => {
        const key = process.env.REACT_APP_BEARER_API_KEY;
        console.log(`Basic ${key}`)
        const requestOptions = {
            method: 'PUT',
            headers: { 'Authorization': `Basic ${key}`,
                        'Content-Type': 'application/json'},
            body: JSON.stringify({
                "grant_type":"client_credentials",
                "scope":{
                "userId":"SFADMIN", 
                "companyId":"talenteam",
                "userType":"user",
                "resourceType":"learning_public_api"
                }
                }),
        };
        fetch('https://talenteam-stage.plateau.com/learning/oauth-api/rest/v1/token', requestOptions)
            .then(response => response.json())
            .then(response => setBearerToken(response))
            .catch(error => console.log(error));

            
        console.log(bearerToken);
    }, [])


    return(
        <div>

        </div>
    )   
}

export default GetBearerToken;